<template>
  <div>
    <v-app-bar
      app
      dense
      height="45px"
    >
      <v-app-bar-title>
        <h1>{{ restaurantName }} </h1>
      </v-app-bar-title>
      <v-spacer/>
      <v-toolbar-items/>
    </v-app-bar>
    <div
      style="display: grid;grid-template-columns:  1fr 1fr;overflow: hidden; height: 724px"
    >
      <div
        class="ml-2 pt-2"
      >
        <v-card>
          <v-card-title class="align-center px-4 pt-4 pb-1">
            <h3>{{ $t('跑堂日结单') }}</h3>
            <v-spacer/>
            <v-btn
              class="text-h3 d-flex align-center justify-center"
              bist
              du
              sicher
              dark
              color="#4D8AED"
              height="41"
              width="258"
              @click="printAllServantSummaryForToday"
            >
              {{ $t('打印全部跑堂日结单') }}
            </v-btn>
          </v-card-title>

          <div
            id="servantCards"
            style="overflow: scroll; "
            :style="localServantList.length>4 ? {'height': 'calc(100vh - 128px'} : {'height': 'calc(100vh - 136px)'}"
            class="mt-2 mx-4"
          >
            <div
              style="display: grid;
              grid-template-columns: repeat(2,1fr);
              grid-auto-rows:305px ;
              grid-gap: 4px;
              min-height: calc(90vh - 120px)"
            >
              <div
                v-for="s in localServantList"
                :key="s.servant.id"
                style="background: #f0f0f0;overflow: visible; border-radius: 16px; "
                :style="activeId===s.servant.id ? {zIndex: 100}:{ }"
                @click="changeActiveId(s.servant.id)"
              >
                <div
                  style="background: #f0f0f0;border: 4px solid black;border-radius: 16px;"
                  class="pa-2"
                >
                  <div class="d-flex justify-space-between align-center">
                    <div class="text-h3">
                      {{ s.servant.name }}
                    </div>
                    <v-btn
                      class="mr-0"
                      small
                      color="warning"
                      @click="printServantSummaryForToday(s.servant.password)"
                    >
                      {{ $t('打印日结单') }}
                    </v-btn>
                  </div>

                  <div style="height: 2px;width: 100%;background: black"/>

                  <!--                  <div :style="activeId===s.servant.id ? {zIndex:100}:{zIndex: 1}">-->
                  <div>
                    <div class="py-2">
                      <div
                        v-for="pay in fillPayMethodTotal(s.payMethodTotal,activeId!==s.servant.id)"
                        :key="pay.id"
                        class="d-flex justify-space-between pt-1"
                      >
                        <div class="label">
                          {{ $t(pay.name) }}
                        </div>
                        <div class="value font-weight-bold">
                          {{ pay.amount | priceDisplay }}
                        </div>
                      </div>

                      <div
                        class="d-flex justify-start py-1"
                      >
                        <div
                          v-if="activeId!==s.servant.id"
                          style="border-bottom: 1px solid #4D8AED;color: #4D8AED"
                        >
                          {{ $t('展示更多') }}
                        </div>
                        <div
                          v-else
                          style="border-bottom: 1px solid #4D8AED;color: #4D8AED"
                        >
                          {{ $t('收起') }}
                        </div>
                      </div>
                    </div>

                    <div style="height: 2px;width: 100%;background: black"/>

                    <div
                      class="d-flex justify-space-between pt-1"
                    >
                      <div class="label">
                        {{ $t('小费') }}
                      </div>
                      <div class="value font-weight-bold">
                        {{ s.tipIncome ? s.tipIncome : 0 | priceDisplay }}
                      </div>
                    </div>

                    <div
                      class="d-flex justify-space-between pt-1 "
                    >
                      <div class="label">
                        {{ $t('今日总计(不含小费)') }}
                      </div>
                      <div class="value font-weight-bold">
                        {{ s.todayTotal ? s.todayTotal : 0 | priceDisplay }}
                      </div>
                    </div>

                    <div
                      class="d-flex justify-space-between pt-1"
                    >
                      <div class="label">
                        {{ $t('未结账') }}
                      </div>
                      <div class="value font-weight-bold">
                        {{ s.notPay | priceDisplay }}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </v-card>
      </div>

      <div class="pt-2 pl-2 pr-2">
        <v-card>
          <v-card
            flat
          >
            <div
              style="display: grid; grid-template-columns: 1fr 1fr; grid-column-gap: 12px"
              class="px-4 pt-4 pb-2"
            >
              <v-btn
                class="text-h3"
                dark
                block
                color="orange"
                @click="printXbon"
              >
                {{ $t('打印XBON') }}
              </v-btn>

              <v-btn
                class="text-h3"
                color="green"
                dark
                block
                @click="printZbonToday"
              >
                {{ $t('打印ZBON') }}
              </v-btn>
            </div>
          </v-card>
          <v-card
            class="px-4 pb-2 "
            flat
          >
            <h3>{{ $t('上次ZBon打印') }}</h3>
            <h2>{{ lastZbonTime }} ── {{ $t('到目前为止') }}</h2>
            <h2/>
          </v-card>
        </v-card>

        <div
          style="display: grid;grid-template-columns:repeat(2,1fr);grid-gap: 4px;"
          class="mt-2"
        >
          <v-card
            height="234px"
          >
            <div class="mx-4 mt-3">
              <h2>{{ $t('营业额基本情况') }}</h2>
            </div>
            <v-card-text class="pt-1 pb-1">
              <div class="d-flex justify-space-between py-1">
                <div class="label">
                  {{ $t('totalMoneyCount') }}
                </div>
                <div class="value font-weight-bold">
                  {{ umsatzInUndAus.total | priceDisplay }}
                </div>
              </div>

              <div class="d-flex justify-space-between py-1">
                <div class="label">
                  {{ $t('堂食(食品)') }}
                </div>
                <div class="value font-weight-bold">
                  {{ umsatzInUndAus.inHauseP7 | priceDisplay }}
                </div>
              </div>
              <div class="d-flex justify-space-between py-1">
                <div class="label">
                  {{ $t('堂食(酒水)') }}
                </div>
                <div class="value font-weight-bold">
                  {{ umsatzInUndAus.inHauseP19 | priceDisplay }}
                </div>
              </div>
              <div class="d-flex justify-space-between py-1">
                <div class="label">
                  {{ $t('外卖(食品)') }}
                </div>
                <div class="value font-weight-bold">
                  {{ umsatzInUndAus.aussHauseP7 | priceDisplay }}
                </div>
              </div>
              <div class="d-flex justify-space-between py-1">
                <div class="label">
                  {{ $t('外卖(酒水)') }}
                </div>
                <div class="value font-weight-bold">
                  {{ umsatzInUndAus.aussHauseP19 | priceDisplay }}
                </div>
              </div>
            </v-card-text>
          </v-card>

          <v-card
            style="overflow: visible; z-index: 50"
            color="white"
          >
            <div class="mx-4 mt-3 d-flex justify-space-between">
              <h2>{{ $t('支付方式') }}</h2>

              <h2>{{ $t('总额(数量)') }}</h2>
            </div>

            <v-card-text class="pt-1 pb-1">
              <div
                @click="showTotalPayMentSum=!showTotalPayMentSum"
              >
                <template v-for="pay in paidInfoTotal(paidInfo,showTotalPayMentSum)">
                  <div
                    :key="pay.id"
                    class="d-flex justify-space-between py-1"
                  >
                    <div class="label">
                      {{ $t(pay.paidName) }}
                    </div>
                    <div class="value font-weight-bold">
                      {{ pay.paidTotal | priceDisplay }}({{ pay.paidCount }})
                    </div>
                  </div>
                </template>

                <div
                  v-if="showTotalPayMentSum"
                  class="d-flex justify-start py-1 "
                >
                  <div
                    style="border-bottom: 1px solid #4D8AED;color: #4D8AED"
                    class="label"
                  >
                    {{ $t('展示更多') }}
                  </div>
                </div>
                <div
                  v-else
                  class="d-flex justify-start py-1"
                >
                  <div
                    style="border-bottom: 1px solid #4D8AED ;color: #4D8AED"
                    class="label"
                  >
                    {{ $t('收起') }}
                  </div>
                </div>
              </div>
            </v-card-text>
          </v-card>

          <div
            style="position: absolute; top: 388px; width: calc(50% - 16px) "
          >
            <div style="">
              <v-card
                class="px-4 pt-3 pb-4"
              >
                <h2 class="mb-1">
                  {{ $t('Umsatz') }}
                </h2>

                <div
                  style="display: grid; grid-template-columns:  80px 100px 65px repeat(2,1fr) 55px;
                          font-size: 18px; font-weight: bold; border-bottom: 2px solid black; "
                >
                  <template
                    v-for="(header,h) in umsatzTableHeaders"
                  >
                    <div
                      :key="'header'+h"
                    >
                      {{ $t(header.text) }}
                    </div>
                  </template>
                </div>

                <div style="border-bottom: 2px solid black; border-radius: 0">
                  <template
                    v-for="(line,i) in salesItems"
                  >
                    <div
                      :key="'line'+i"
                      style="display: grid; grid-template-columns: 80px 100px 65px repeat(2,1fr) 55px; font-size: 16px"
                      class="py-1"
                    >
                      <template
                        v-for="(item,h) in line"
                      >
                        <div
                          :key="'item'+h"
                          :style="{ borderLeft: h > 0 ? '2px solid black': ''}"
                        >
                          <!--                          {{ $t(item) }}-->
                          {{ item }}
                        </div>
                      </template>
                    </div>
                  </template>
                </div>
              </v-card>

              <v-card
                class="pa-4 pb-6 mt-2"
                style="display: grid; grid-template-columns: 1fr 1fr; grid-column-gap: 12px"
              >
                <div>
                  <div class="d-flex justify-space-between pt-3 pb-4">
                    <div class="label">
                      {{ $t('折扣总额') }}
                    </div>
                    <div class="value font-weight-bold">
                      {{ todayBill.discountSum.split('-').pop() }}
                    </div>
                  </div>
                  <div>
                    <v-btn
                      dark
                      block
                      color="warning"
                      class="text-h3"
                      @click="handleDiscount"
                    >
                      {{ $t('查看折扣明细') }}
                    </v-btn>
                  </div>
                </div>

                <div>
                  <div class="d-flex justify-space-between pt-3 pb-4">
                    <div class="label">
                      {{ $t('退菜总额') }}
                    </div>
                    <div class="value font-weight-bold">
                      {{ todayBill.stornSum.split('-').pop() }}
                    </div>
                  </div>
                  <div>
                    <v-btn
                      dark
                      block
                      color="success"
                      class="text-h3"
                      @click="handleReturn"
                    >
                      {{ $t('查看退菜明细') }}
                    </v-btn>
                  </div>
                </div>
              </v-card>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!--    <div-->
    <!--      v-if="localServantList.length>4"-->
    <!--      style="position: fixed; bottom: 20px; left: 230px"-->
    <!--    >-->
    <!--      <v-btn-->
    <!--        fab-->
    <!--        small-->
    <!--      >-->
    <!--        <v-icon>{{ isScrollToBottom ? 'mdi-arrow-up' : 'mdi-arrow-down' }}</v-icon>-->
    <!--      </v-btn>-->
    <!--    </div>-->

    <v-dialog
      v-model="showDiscountDialog"
      width="80vw"
      persistent
    >
      <v-card height="82vh">
        <v-card-title class="mb-2">
          <h2>{{ $t('折扣记录') }}</h2>
        </v-card-title>
        <v-card-text>
          <v-data-table
            :headers="i18nObjectTrans(discountOrderHeaders)"
            :items="discountItems"
            height="60vh"
            fixed-header
            item-key="discountId"
            show-expand
            single-expand
          >
            <template #item.value="{item}">
              <h4>{{ item.value | priceDisplay }}</h4>
            </template>
            <template
              #expanded-item="{headers,item}"
            >
              <template v-if="item.detailsInfo.length>0">
                <td colspan="1"/>
                <td :colspan="headers.length-1">
                  <div class="my-1">
                    <v-data-table
                      hide-default-footer
                      :headers="i18nObjectTrans(discountOrderDetailInfoHeaders)"
                      :items="i18nObjectTrans(item.detailsInfo)"
                    />
                  </div>
                </td>
              </template>
            </template>
          </v-data-table>
        </v-card-text>
        <div style="position: absolute; top: 20px; right: 20px">
          <v-btn
            class="primary "
            @click="showDiscountDialog=!showDiscountDialog"
          >
            {{ $t('return') }}
          </v-btn>
        </div>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="showReturnDishDialog"
      width="80vw"
      persistent
    >
      <v-card height="82vh">
        <v-card-title>
          <h2>{{ $t('退菜记录') }}</h2>
        </v-card-title>

        <v-card-text>
          <v-data-table
            :headers="i18nObjectTrans(stornOrderHeaders)"
            :items="stornItems"
            fixed-header
            height="60vh"
            item-key="id"
            show-expand
            single-expand
          >
            <template #item.value="{item}">
              <h4>{{ item.value | priceDisplay }}</h4>
            </template>
            <template
              #expanded-item="{headers,item}"
            >
              <template>
                <td colspan="1"/>
                <td :colspan="headers.length-1">
                  <div class="my-1">
                    <v-data-table

                      hide-default-footer
                      :headers="i18nObjectTrans(stornOrderDetailsHeaders)"
                      :items="i18nObjectTrans(item.detailsInfo)"
                    />
                  </div>
                </td>
              </template>
            </template>
          </v-data-table>
        </v-card-text>
        <div style="position: absolute; top: 20px; right: 20px">
          <v-btn
            class="primary"
            @click="showReturnDishDialog=!showReturnDishDialog"
          >
            {{ $t('return') }}
          </v-btn>
        </div>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>

  import { getRestaurantInfo, isNotBlank } from '@/components/Utlis/api'
  import dayjs from 'dayjs'
  import {
    getAllDashBoardInfoToday,
    getDiscountAndStornOrders,
    getLastZbonSlotIndexInfos,
    getStartAndEndTimeForToday,
    i18nObjectTrans,
    printAllServantSummaryForToday,
    printServantSummaryForToday,
    printXbon,
    printZbonToday,
    servantList,
  } from '@/components/Utlis/umsatzInfo'

  import Payment from '@/model/Payment'
  import {
    discountDetailsHeaders,
    discountHeaders,
    stornDetailsHeaders,
    stornHeaders,
    umsatzHeaders,
  } from '@/model/Dashboard'
  import _ from 'lodash'

  export default {
    name: 'DashboardNewVue',
    data () {
      return {
        lastZbonTime: '',
        expanded: [],
        restaurantName: '',
        todayDate: dayjs().format('YYYY-MM-DD'),
        activeId: null,
        showReturnDishDialog: false,
        showDiscountDialog: false,
        dashBoardInfo: {
          taxInfos: {
            discountSum: '',
            stornSum: null,
            fValue: null,
            fValue2: null,
          },
          salesInfo: {},
          spanTurnover: {},
          dishStatistic: [],

        },
        billData: {
          content: {
            taxInfos: [],
            fTotal: 0,
            fTotalTax: 0,
            fTotalTe: 0,
            paidInfo: [],
            discount: [],
            storno: [],
          },
          title: {
            timeStart: '',
          },
          localServantList: [],
          fValue: '',
          fValue2: '',
        },

        discountAndStornOrders: [],
        localServantList: [],
        paymentMethodList: [],

        discountOrderDetailInfoHeaders: discountDetailsHeaders(),
        stornOrderDetailsHeaders: stornDetailsHeaders(),

        umsatzTableHeaders: umsatzHeaders(),
        discountOrderHeaders: discountHeaders(),
        stornOrderHeaders: stornHeaders(),
        showTotalPayMentSum: true,
        flag: null,
        // isScrollToBottom: false,

      }
    },
    computed: {
      // isScrollToBottom () {
      //   const nScrollHight = 0
      // },

      billDataContent () {
        return this.billData.content
      },
      taxGroupInfo () {
        const res = this.todayBillContent.taxInfos?.filter(t => t.consumeTypeName === 'Total')
        return res
      },

      todayBill () {
        const res = this.dashBoardInfo.taxInfos
        res.stornSum = res.stornSum ?? ''
        return res
      },
      todayBillContent () {
        const res = this.todayBill.content ?? []
        return res
      },

      paidInfo () {
        const res = this.todayBillContent.paidInfo ?? []

        return res
      },
      discountOrders () {
        const res = this.discountAndStornOrders.filter(i => i.discountStr)
        return res
      },
      cancelOrders () {
        const res = this.discountAndStornOrders.filter(i => i.discountDishCount !== null)
        return res
      },
      salesItems () {
        const res = this.getSalesItems
        // const res = this.getSalesItems()
        return res
      },
      discountItems () {
        const discountList = this.dashBoardInfo.taxInfos.content?.discount

        // console.log('discountList', discountList)

        return discountList?.map(item => {
          let itemDetailInfo = item.detailsInfo?.map((j) => {
            // 数据德语字母显示
            j.sumTempDiscountMod = j.sumTempDiscountMod.replace('.', ',')
            console.log('temp', j)
            return j
          })

          // 特殊情况，有单菜品折扣也有整单百分比则扣，直接去总账单观看数据
          if (item.detailsInfo?.length > 0 && item.orderInfo.discountStr?.includes('p')) {
            itemDetailInfo = []
          }

          if (isNotBlank(item.orderInfo?.discountStr)) {
            itemDetailInfo.push({
              sumCount: 1,
              name: 'Order Discount',
              tempDiscountStr: item.orderInfo.discountStr,
              sumTempDiscountMod: item.orderInfo?.discountStr.includes('p')
                ? ('-' + String(item.orderInfo.value).replace('.', ','))
                : ('-' + String(item.orderInfo.discountStr).replace('.', ',')),
            })
          }

          const discountTypeStr = itemDetailInfo?.length > 0 ? this.$t('单菜品折扣') : this.$t('整单折扣')

          return {
            discountId: item.orderInfo?.id,
            tableName: item.orderInfo?.name,
            // discountType: discountTypeStr,
            discountStr: discountTypeStr,
            timeStamp: item.orderInfo?.time,
            servantName: item.orderInfo?.servantName,
            detailsInfo: itemDetailInfo,
            value: item.orderInfo?.value,

          }
        }).reverse()
      },

      stornItems () {
        const stornoList = this.dashBoardInfo.taxInfos.content?.storno
        const stornOrderIds = _.uniq(stornoList?.map(i => i.orderId))

        const tempStornItems = []
        for (const id of stornOrderIds) {
          const temp = stornoList.filter(i => i.orderId === id)
          const stornSumValue = _.sumBy(temp, function (item) {
            return parseFloat(item.savePrice)
          })
          const stornSumCount = _.sumBy(temp, function (item) {
            return parseInt(item.count) >= 0 ? parseInt(item.count) : -parseInt(item.count)
          })
          const stornOrderDetail = stornoList.filter(i => i.orderId === id)

          const order = this.discountAndStornOrders.find(i => i.orderId === id)

          tempStornItems.push({
            ...order,
            stornSumValue,
            stornOrderDetail,
            stornSumCount,
          })
        }

        return _.orderBy(tempStornItems.map(item => {
          return {
            id: item.orderId,
            tableName: item.tableName,
            count: item.stornSumCount,
            // Dname: item.Dname,
            // returnReason: item.returnReason,
            time: item.updateTimestamp,
            servantName: item.servantName,
            detailsInfo: item.stornOrderDetail,
            value: item.stornSumValue,
          }
        }), ['time'], ['desc'])
      },

      umsatzInUndAus () {
        const aussHause = this.todayBillContent.taxInfos?.filter(t => t.consumeTypeName === 'Außer Haus').reduce((amount, i) => {
          amount += parseFloat(i.taxGroupTotal)
          return amount
        }, 0)
        const aussHauseP19 = this.todayBillContent.taxInfos?.filter(t => t.consumeTypeName === 'Außer Haus' && t.taxRatePercentage === 19).reduce((amount, i) => {
          amount += parseFloat(i.taxGroupTotal)
          return amount
        }, 0)
        const aussHauseP7 = this.todayBillContent.taxInfos?.filter(t => t.consumeTypeName === 'Außer Haus' && t.taxRatePercentage === 7).reduce((amount, i) => {
          amount += parseFloat(i.taxGroupTotal)
          return amount
        }, 0)

        const inHause = this.todayBillContent.taxInfos?.filter(t => t.consumeTypeName === 'In Haus').reduce((amount, i) => {
          amount += parseFloat(i.taxGroupTotal)
          return amount
        }, 0)

        const inHauseP19 = this.todayBillContent.taxInfos?.filter(t => t.consumeTypeName === 'In Haus' && t.taxRatePercentage === 19).reduce((amount, i) => {
          amount += parseFloat(i.taxGroupTotal)
          return amount
        }, 0)
        const inHauseP7 = this.todayBillContent.taxInfos?.filter(t => t.consumeTypeName === 'In Haus' && t.taxRatePercentage === 7).reduce((amount, i) => {
          amount += parseFloat(i.taxGroupTotal)
          return amount
        }, 0)

        const total = aussHauseP19 + aussHauseP7 + inHauseP19 + inHauseP7

        return {
          inHause: inHause,
          inHauseP7: inHauseP7,
          inHauseP19: inHauseP19,
          aussHause: aussHause,
          aussHauseP7: aussHauseP7,
          aussHauseP19: aussHauseP19,
          total: total,
        }
      },

      getSalesItems () {
        const umsatz = {
          name: 'Umsatz',
          all: this.todayBillContent.fTotal ?? 0,
          p100: 0,
          p19: 0,
          p7: 0,
          p0: 0,
        }
        const netto = {
          name: 'Netto',
          all: this.todayBillContent.fTotalTe ?? 0,
          p100: 0,
          p19: 0,
          p7: 0,
          p0: 0,
        }
        const steuer = {
          name: 'Steuer',
          all: this.todayBillContent.fTotalTax ?? 0,
          p100: 0,
          p19: 0,
          p7: 0,
          p0: 0,
        }

        if (this.taxGroupInfo) {
          this.taxGroupInfo.forEach(tax => {
            umsatz['p' + tax.taxRatePercentage] = tax.groupTotal ?? 0
            netto['p' + tax.taxRatePercentage] = tax.nettoumsatz ?? 0
            steuer['p' + tax.taxRatePercentage] = tax.umsatzsteuer ?? 0
          })
        }

        return [umsatz, netto, steuer]
      },

    },
    async mounted () {
      this.paymentMethodList = await Payment.getList()
      await this.loadData()

      this.localServantList = await this.servantList()
      this.discountAndStornOrders = await this.getDiscountAndStornOrders()

      this.lastZbonTime = (await getStartAndEndTimeForToday())[0]
    },
    methods: {

      async loadData () {
        await this.onceData()
        this.billData = await getLastZbonSlotIndexInfos()

        this.dashBoardInfo = await getAllDashBoardInfoToday()
        console.log('dashBoardInfo', this.dashBoardInfo)

        this.dashBoardInfo.taxInfos.discountSum = this.dashBoardInfo.taxInfos.fValue2
        this.dashBoardInfo.taxInfos.stornSum = this.dashBoardInfo.taxInfos.fValue

        this.localServantList = await this.servantList()
        this.discountAndStornOrders = await this.getDiscountAndStornOrders()
      },
      async onceData () {
        const res = await getRestaurantInfo()
        let arr = []
        arr = res.content[0].name.split('<BR>')
        this.restaurantName = arr.join(' ')
      },

      servantList,
      getDiscountAndStornOrders,

      async handleReturn () {
        this.showReturnDishDialog = true
        await this.loadData()
      },

      async handleDiscount () {
        this.showDiscountDialog = true
        await this.loadData()
      },

      paidInfoTotal (paidInfo, withFilter = false) {
        // console.log('paidInfo', paidInfo)
        // 9 tips
        // const tempPaidInfo = this.paymentMethodList.filter(p => p.id > 0 && p.id !== 9).map(p => {
        const tempPaidInfo = this.paymentMethodList.filter(p => p.id > 0 && parseInt(p.id) !== 9).map(p => {
          const r = paidInfo.find(t => {
            return parseInt(t.id) === parseInt(p.id)
          })
          return {
            paidName: p._langsname,
            paidCount: r?.paidCount ?? 0,
            paidTotal: r?.paidTotal ?? 0,
            id: p.id,
          }
        })

        tempPaidInfo.push((this.paymentMethodList.filter(p => parseInt(p.id) === 9).map(p => {
          const r = paidInfo.find(t => {
            return parseInt(t.id) === 9
          })
          return {
            paidName: p._langsname,
            paidCount: r?.paidCount ?? 0,
            paidTotal: r?.paidTotal ?? 0,
            id: 9,
          }
        }))[0])

        // console.log('paidInfoTotal Kellner', tempPaidInfo)

        const idUpperLimit = 5
        if (withFilter) {
          const paidInfoArr = tempPaidInfo.filter(p => p?.id > idUpperLimit)
          const othersPaidTotal = paidInfoArr.reduce((amount, item) => {
            amount += parseFloat(item?.paidTotal ?? 0)
            return amount
          }, 0)

          const othersCount = paidInfoArr.reduce((sum, item) => {
            sum += parseInt(item?.paidCount ?? 0)
            return sum
          }, 0)

          const filter = (p) => p?.id >= 1 && p?.id <= idUpperLimit
          const filteredPaidInfo = tempPaidInfo.filter(filter, 0)

          const res = filteredPaidInfo.map(p => {
            const r = this.paymentMethodList.find(i => i.id === parseInt(p.id), 0)
            return {
              paidName: r._langsname,
              paidCount: p.paidCount,
              paidTotal: p.paidTotal,
              id: r.id,
            }
          })

          return [...res,
                  {
                    paidName: 'Others',
                    paidCount: othersCount,
                    paidTotal: othersPaidTotal,
                    id: '-1',
                  },
          ]
        } else {
          return tempPaidInfo
        }
      },
      fillPayMethodTotal (payMethod, withFilter = true) {
        if (withFilter) {
          const filter = (p) => parseInt(p.id) >= 1 && parseInt(p.id) <= 5
          const included = this.paymentMethodList.filter(filter)

          const res = included.map(p => {
            const r = payMethod.find(t => parseInt(t.payMethodId) === parseInt(p.id))
            return {
              name: p._langsname,
              amount: r?.sumTotal ?? 0,
              id: p.id,
            }
          })

          const otherAmount = payMethod.filter(p => parseInt(p.payMethodId) > 5 && parseInt(p.payMethodId) !== 9).reduce((amount, i) => {
            amount += parseFloat(i.sumTotal)
            return amount
          }, 0)

          return [...res, {
            name: 'Others',
            amount: otherAmount,
            id: '-1',
          }]
        } else {
          // return this.paymentMethodList.filter(p => p.id > 0 && parseInt(p.id) !== 9).map(p => {
          return this.paymentMethodList.filter(p => parseInt(p.id) > 0 && parseInt(p.id) !== 9).map(p => {
            const r = payMethod.find(t => parseInt(t.payMethodId) === parseInt(p.id))
            return {
              name: p._langsname,
              amount: r?.sumTotal ?? 0,
              id: p.id,
            }
          })
        }
      },
      changeActiveId (activeId) {
        if (this.activeId === activeId) {
          this.activeId = null
        } else {
          this.activeId = activeId
        }
      },

      printAllServantSummaryForToday,
      printServantSummaryForToday,
      printZbonToday,
      printXbon,
      getStartAndEndTimeForToday,

      i18nObjectTrans,

      // i18nObjectTrans (list) {
      //   const res = list.map(item => {
      //     item.text = this.$t(item.text)
      //     return item
      //   })
      //   return res
      // },
    },
  }
</script>

