// import i18n from '@/i18n'

export function umsatzHeaders () {
  const header = [
    {
      text: 'Name',
      align: 'start',
      value: 'name',
    },
    {
      text: 'AllUmsatz',
      value: 'all',
    },
    {
      text: '100%',
      value: 'p100',
    },
    {
      text: '19%',
      value: 'p19',
    },
    {
      text: '7%',
      value: 'p7',
    },
    {
      text: '0%',
      value: 'p0',
    },

  ]
  const headerNew = header.map(h => {
    return {
      ...h,
      class: 'font-size: 20px',
    }
  })

  return headerNew
}

